.layout-switcher {
  position: fixed;
  font-size: 12px;
  z-index: 100; }
  .layout-switcher a {
    cursor: pointer; }
  .layout-switcher .opacity-area {
    opacity: 0.1; }
  .layout-switcher .button-group .button {
    margin: 5px;
    padding: 5px;
    display: block; }
  .layout-switcher:hover .opacity-area {
    opacity: 0.7; }

.task-card-modal-demo {
  position: absolute;
  z-index: 9999;
  transform: translateX(-50%);
  left: 50%;
  outline: none;
  overflow-y: scroll;
  height: 100%; }
  .task-card-modal-demo iframe {
    display: block;
    margin: 0 auto;
    margin-top: 60px; }
